.GoalTable {
  max-width: 1400px;
  margin: auto;
  padding-left: 80px;
  padding-right: 40px;
  margin-top: 50px;
  display: grid;
  gap: 50px;
  grid-template-columns: repeat(auto-fill, 265px);

  @media screen and (max-width: 600px) {
    padding:0;
    display: flex;
    flex-direction: column;
    width: 270px;
  }
}